import React, { Component } from 'react';
import Subject from "./components/Subject"
import Listt from "./components/Listt"
import './App.css';

class App extends Component {
  constructor(props){ //컴포넌트 실행하는 render보다 먼저 실행되면서, 컴포넌트초기화담당
    super(props);
    this.state={//state값 초기화, 서브젝트값을 스테이트화시킴
      subject:{bigtitle:'Hello' , smalltitle:"react is very nice!!"}

    }
  }
  render(){
    return (
      <div className="App">
        <Subject bigtitle={this.state.subject.bigtitle}
                smalltitle={this.state.subject.smalltitle}></Subject>

        <Subject bigtitle="oh" smalltitle="i agree that!"></Subject>
        <Listt></Listt>
      </div>
    );
  }
}

export default App;
